.container {
  flex: 0 1 auto;
  width: 60%;
  display: flex;
  justify-content: flex-end;
}

.statistics {
  flex: 0 1 auto;
  max-width: 730px;
  width: 100%;
  display: flex;
  flex-direction: column;
  margin: 0px 16px;
  margin-top: 32px;
}

.title {
  color: #fbfbfb;
  text-align: left;
  font-size: 30px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

.chartContainer {
  width: 100%;
  max-width: 730px;
  margin-top: 20px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  gap: 15px;
}

.chartBalance {
  position: relative;
  flex: 0 1 auto;
  width: 100%;
  height: 100%;
  max-width: 250px;
  max-height: 250px;
}

.balance {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  color: #fbfbfb;
  text-align: center;
  font-size: 18px;
  font-weight: 600;
}

.statsContainer {
  position: relative;
  width: 100%;
  max-width: 395px;
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.inputs {
  position: absolute;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  gap: 15px;
  width: 100%;
}

.dropdown {
  position: relative;
  width: 100%;
  max-width: 182px;
}

.dropdownHeader {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 8px;
  background: transparent;
  color: rgba(255, 255, 255, 0.6);
  cursor: pointer;
  border-radius: 10px;
  border: solid 1px rgba(255, 255, 255, 0.4);
}

.dropdownHeader span {
  color: #fbfbfb;
  font-size: 16px;
  font-weight: 400;
}

.arrow {
  transition: transform 0.2s ease;
  width: 20px;
  height: 20px;
}

.open {
  transform: rotate(180deg);
}

.dropdownContainer {
  position: absolute;
  left: 0;
  right: 0;
}

.dropdownList {
  position: relative;
  top: 0;
  max-height: 164px;
  overflow-y: auto;
  background: rgba(88, 74, 122, 0.8);
  border-radius: 4px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
  z-index: 1000;
}

.dropdownOption {
  color: #fbfbfb;
  font-size: 16px;
  font-weight: 400;
  padding: 8px 16px;
  cursor: pointer;
  backdrop-filter: blur(10px);
}

.dropdownOption:hover,
.dropdownOption:focus {
  background: rgba(255, 255, 255, 0.2);
  color: #ff868d;
}

.categoryListContianer {
  margin-top: 70px;
  display: flex;
  flex-direction: column;
  width: 100%;
}

.categoryTitleContainer {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding: 0px 16px;
  width: 100%;
  height: 56px;
  border-radius: 8px;
  background: rgba(82, 59, 126, 0.6);
  box-shadow: 0px 4px 60px 0px rgba(0, 0, 0, 0.25);
  backdrop-filter: blur(50px);
}

.categoryTitle {
  color: #fbfbfb;
  font-size: 16px;
  font-weight: 600;
}

.categoryList {
  flex: 0 1 auto;
  width: 100%;
  display: flex;
  flex-direction: column;
  overflow: auto;
  height: 290px;
}

.tableRow {
  display: flex;
  justify-content: space-between;
  padding: 16px;
  color: #fbfbfb;
  border-bottom: 1px solid rgba(255, 255, 255, 0.41);
}

.tableBreaker {
  display: flex;
  flex-direction: row;
  gap: 10px;
}

.tableCell {
  display: flex;
  align-items: center;
}

.tableCellColor {
  display: flex;
  align-items: center;
  width: 24px;
  height: 24px;
  border-radius: 2px;
  /* background: #fed057; */
}

.balanceStatsContainer {
  width: 100%;
  margin: 25px 0px;
  display: flex;
  flex-direction: column;
  gap: 19px;
}

.balanceStats {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding-left: 16px;
  padding-right: 30px;
}

.balanceStatsText {
  color: #fbfbfb;
  font-size: 14px;
  font-weight: 600;
  height: 18px;
}

.odd {
  color: #ff868d;
}

.even {
  color: #ffb627;
}

.noCategory {
  color: #fbfbfb;
  font-size: 16px;
  font-weight: 400;
  text-align: center;
}

.resetButton {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 2px;
  font-size: 16px;
  background: transparent;
  color: #fbfbfb;
  cursor: pointer;
  border-radius: 8px;
  border: solid 1px rgba(255, 255, 255, 0.4);
}

.chartBreaker {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

@media (max-width: 1024px) and (min-width: 769px) {
  .container {
    width: 100%;
  }

  .statistics {
    max-width: none;
    width: 100%;
  }

  .chartContainer {
    max-width: none;
  }

  .statsContainer {
    max-width: 600px;
  }

  .dropdown {
    position: relative;
    width: 100%;
    max-width: 240px;
  }
}

@media (max-width: 768px) and (min-width: 425px) {
  .container {
    width: 100%;
  }

  .statistics {
    max-width: none;
    width: 100%;
  }

  .chartContainer {
    flex-direction: column;
    max-width: none;
    align-items: center;
    gap: 32px;
  }

  .statsContainer {
    max-width: none;
  }

  .dropdown {
    max-width: 240px;
  }

  .chartBalance {
    max-width: 280px;
    max-height: 280px;
  }
}

@media (max-width: 424px) {
  .container {
    width: 100%;
  }

  .statistics {
    max-width: none;
    width: 100%;
  }

  .chartContainer {
    margin-top: 10px;
    flex-direction: column;
    max-width: none;
    align-items: center;
    gap: 32px;
  }

  .statsContainer {
    max-width: none;
  }

  .inputs {
    flex-direction: column;
    gap: 20px;
  }

  .dropdown {
    max-width: none;
  }

  .chartBalance {
    max-width: 280px;
    max-height: 280px;
  }

  .categoryListContianer {
    margin-top: 126px;
  }

  .categoryList {
    height: auto;
  }
}
